import React from 'react'

import whiteHamburger from "../assets/images/white-hamburger.svg";
import hamburger from "../assets/images/hamburger.svg";
import logo from "../assets/images/logo.svg";
import circleBlack from "../assets/images/dropdown-circle-black.svg";
import blackClose from '../assets/images/black-close.svg';

import whiteClose from "../assets/images/white-close.svg" ;
class Header extends React.Component {
    render() {
        return (
            <section> 
                <div className="header fixed-header-dark">
                    <div className="ham-menu show-mobile">
                        <img src={hamburger} />
                    </div>
                    <div className="nav-items">
                        <a href="/ourStory" className="nav-item nav-story">Our Story</a>
                        <a className="nav-item nav-madSwap">MadSwap</a>
                        <a className="nav-item nav-how">How It Works</a>
                        <a className="nav-item nav-wardrobe">Wardrobe</a>
                        <a className="nav-item nav-faq">FAQ</a>
                    </div>
                    <div className="logo">
                    <a href="/"><img src={logo}/></a>
                    </div>
                    <div className="button-wrapper">
                        <a href="https://madthread.com/join-the-waitlist" className="button">SIGN ME UP</a>
                    </div>
                    
                    <div className="ham-menu show-mobile">
                        <img src={whiteHamburger} style={{visibility: 'hidden'}}/>
                    </div>
                    
                </div>
                <div className="scroll-top-btn">
                    <img src={circleBlack} />
                </div>
                <div className="header">
                    <div className="ham-menu show-mobile">
                    <img src={whiteHamburger} />
                    </div>
                    <div className="nav-items">
                    <a href="/ourStory" className="nav-item nav-story">Our Story</a>
                    <a className="nav-item nav-madSwap">MadSwap</a>
                    <a className="nav-item nav-how">How It Works</a>
                    <a className="nav-item nav-wardrobe">Wardrobe</a>
                    <a className="nav-item nav-faq">FAQ</a>
                    </div>
                    <div className="logo">
                    <a href="/"><img src={logo} /></a>
                    </div>
                    <div className="button-wrapper">
                    <a href="https://madthread.com/join-the-waitlist" className="button">SIGN ME UP</a>
                    </div>
                </div> 
                <div className="side-menu show-mobile-flex">
                    <img className="close-side" src={whiteClose}/>
                    <a href="/ourStory" className="nav-item nav-story">Our Story</a>
                    <a className="nav-item nav-madSwap">MadSwap</a>
                    <a className="nav-item nav-how">How It Works</a>
                    <a className="nav-item nav-wardrobe">Wardrobe</a>
                    <a className="nav-item nav-faq">FAQ</a>
                </div>
                

                <div className="modal-container wardrobe-modal">
                    <div className="modal-window">
                        <div className="clothing"></div>
                        <p className="brand"></p>
                        <p className="name"></p>
                        <p className="price">Retail SGD$ <span className="price-val"></span></p>
                        <img className="close-button" src={blackClose} />
                    </div>
                </div>
                
                <div className="modal-container contact-modal">
                    <div className="modal-window">
                    </div>
                </div>
            </section>
        )
    }
}

export default Header
